body {
  margin: 0;
  padding: 0;
  font-family: "badaboom";
    ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



@font-face{
  font-family: "badaboom";
  src: url(/Clicky-Game/static/media/BADABB__.0431c28f.TTF);
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 170px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 170px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: hidden;
  }

  img{
      height: 160px;
      width: 160px;
  }
  

   .hover img {
      margin-left: 10px;
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
  }
  .hover img:hover  {
      margin-left: -30px;
  }
.wrapper {
    padding-top: 10px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
    background-image: url(/Clicky-Game/static/media/marvel.91d7f614.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;

  }
.gameScore {
    text-align: center;
    height: 70px;
    position: relative;
    line-height: 1em;
    padding: 0;
    margin: 0;
  }
  
  
  h3.score {
    margin-top: 5px;
    padding: 0;
    font-weight: 600;
  }
  h3.status{
    margin: 0;
    padding: 0;
    color: red;
  }
.App-header {
  background-color: blue;
  height: 80px;
  padding: 10px;
  color: red;
}

.App-title {
  font-size: 2em;
}

.App-intro {
  font-size: large;
}

footer{
  background-color: blue;
  height: 80px;
  padding: 10px;
  color: red;
}

